<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    props: {
        coin: {
            type: Object,
            required: true
        },
        buyCoinLink: {
            type: String,
            default: null
        }
    },
    watch: {
        coin(newVal, oldVal) {
            // // console.log(newVal, oldVal)
            this.loadData()
        }
    },
    setup() {
        
    },
    data: () => ({
        coinStatistics: {
            // totalCoinsCount: 124245563,
            // coinPrice: 2700,
            // capitalization: 100000000000,
            // holdersCount: 436546,
            // transactionsCount: 833243252988,
            // contract: 'TDSGdgre4356dsUJFGerg65GXgfhjty',
            // decimals: 18
        }
    }),
    methods: {
        async loadData(){
            try {
                const data = {coinTicker: this.coin.ticker}
                this.coinStatistics = await this.$store.dispatch('getCoinData', data)
            } catch(e) {
                this.$error(e)
            } 
        }
    },
    async mounted() {
        this.loadData()
        // get coin statistics
    }
})
</script>

