<template>
    <div class="landing-coin">
    <CoinSection
    :class="'coin-section'"
    :id="'coin-section-'+coinName"
    :coinName="coinName"
    :title="coinName"
    :textStyle="coinName=='DOM' ? 'text-align: left;' : ''"
    :textHeader="coinName=='DOM' ? $localizeFilter('landing_coin_section_text_header_'+coinName) : null"
    :text="$localizeFilter('landing_coin_section_text_'+coinName)"
    :showCoinStatistics="showCoinStatistics"
    :canBuy="canBuy"
    @buyCoin="buyCoin"
    />

    <ChartSection 
    v-if="showChart"
    :id="chartSectionId"
    :animateChart="animateChart"
    :coinName="coinName"
    :coinTicker="coinTicker"
    />

    <VideoSection 
    v-if="aboutLink != null"
    :id="'video-section-1'+coinName"
    :coinName="coinName"
    :title="$localizeFilter('landing_coin_video_section_1_title_'+coinName)"
    :text="$localizeFilter('landing_coin_video_section_1_text_'+coinName)"
    :link="aboutLink"/>

    <VideoSection 
    v-if="metamaskLink != null"
    :id="'video-section-2'+coinName"
    :coinName="coinName"
    :title="$localizeFilter('landing_coin_video_section_2_title_'+coinName)"
    :text="$localizeFilter('landing_coin_video_section_2_text_'+coinName)"
    :link="metamaskLink"
    :background="'#1D2026'"/>

    <VideoSection 
    v-if="tronLinkLink != null"
    :id="'video-section-2'+coinName"
    :coinName="coinName"
    :title="$localizeFilter('landing_coin_video_section_2_title_'+coinName)"
    :text="$localizeFilter('landing_coin_video_section_2_text_'+coinName)"
    :link="tronLinkLink"
    :background="'#1D2026'"/>

    <VideoSection 
    v-if="trustWalletLink != null"
    :id="'video-section-3'+coinName"
    :coinName="coinName"
    :title="$localizeFilter('landing_coin_video_section_3_title_'+coinName)"
    :text="$localizeFilter('landing_coin_video_section_3_text_'+coinName)"
    :link="trustWalletLink"
    :background="'#1D2026'"/>

    <CoinNewsSection
      v-if="news.length > 0"
      :title="$localizeFilter('landing_news_title') + ' ' + coinName"
      :coinName="coinName"
      :news="news"/>

    <transition name="pop" mode="out-in" v-if="canBuy">
        <BuyCoinPopupWindow v-if="showPopup" :coin="coin" @buyCoin="buyCoin" />
    </transition>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import CoinSection from '@/components/landing/CoinSection.vue'
import VideoSection from '@/components/landing/VideoSection.vue'
import CoinNewsSection from '@/components/landing/CoinNewsSection.vue'
// import LandingFooter from '@/components/landing/LandingFooter.vue'
import localizeFilter from '@/filters/localize.filter'
import ChartSection from '@/components/ChartSection.vue'
import BuyCoinPopupWindow from '@/components/app/BuyCoinPopupWindow.vue';


export default defineComponent({
    props: {
        coinName: {
            required: true
        },
    },
    // watch: {
    //     coinName(newVal, oldVal) {
    //         // console.log(newVal, oldVal)
    //     }
    // },
    data: ()=> ({
        animateChart: false,
        showPopup: false
        // videoLinks: [
        //     {  
        //         coinName: 'DOM',
        //         about:'https://www.youtube.com/embed/L0cJexUGeiY',
        //         metamask: 'https://www.youtube.com/embed/yz56A4lif1Y'

        //     },
        //     {
        //        coinName: 'GadgetCoin',
        //        about: null,
        //        metamask: null
        //     }
        // ]
    }),
    components: {
        CoinSection, ChartSection, VideoSection, CoinNewsSection, BuyCoinPopupWindow
    },
    computed: {
        canBuy() {
            return this.coin.canBuy
        },
        coin() {
          return this.$store.getters.coinByName(this.coinName)
        },
        chartSectionId() {
            return 'chart-section-' + this.coinName
        },
        showChart() {
            // return this.coinName == 'GadgetCoin'
            return this.coinName != 'CuprumToken' && this.coinName != 'TRCoin' && this.coinName != 'OilGasCoalToken' && this.coinName != 'DreamLifeCoin'
            // return false
        },
        showCoinStatistics() {
            return this.coinName != 'CuprumToken' && this.coinName != 'TRCoin' && this.coinName != 'OilGasCoalToken' && this.coinName != 'DreamLifeCoin'
            // return true
        },
        coinTicker() {
            return this.$utils.getCoinTicker(this.coinName)
        },
        aboutLink() {
            if (this.coinName == 'DOM') {
                return 'https://www.youtube.com/embed/L0cJexUGeiY' 
            } 
            else if(this.coinName == 'GadgetCoin') {
                return 'https://www.youtube.com/embed/bNRxWgDh4_8'
            }
            else if(this.coinName == 'InternetCoin') {
                return 'https://www.youtube.com/embed/U4DV3pkHnoQ'
            }
            else if(this.coinName == 'CapitalWhalePresaleToken') {
                return 'https://www.youtube.com/embed/IQfC-pW3x9k'
            }
            else {
                return null
            }
            // this.videoLinks.forEach(link => {
            //     if (link.coinName == this.coinName) {
            //         return link.about
            //     }
            // })
            // return null
        },
        metamaskLink() {
            if (this.coinName == 'DOM') {
                return 'https://www.youtube.com/embed/yz56A4lif1Y' 
            } else {
                return null
            }
        //    this.videoLinks.forEach(link => {
        //         if (link.coinName == this.coinName) {
        //             return link.metamask
        //         }
        //     })
        //     return null
        },
        tronLinkLink(){
            if (this.coinName == 'CapitalWhalePresaleToken') {
                return 'https://www.youtube.com/embed/cC4YahEMono' 
            } else if (this.coinName == 'InternetCoin') {
                return 'https://www.youtube.com/embed/Xon55t9AAQ0' 
            } else {
                return null
            }
        },
        trustWalletLink(){
            if (this.coinName == 'CapitalWhalePresaleToken') {
                return 'https://www.youtube.com/embed/bxjsAdQORkU' 
            } else {
                return null
            }
        },
        news() {
            const coinName = this.coinName
            if (coinName == 'DOM') {
                return []
                // return [{title: localizeFilter('landing_news_1_title_'+coinName),
                // text: localizeFilter('landing_news_1_text_'+coinName),
                // link: 'https://news.google.com/'},

                // {title: localizeFilter('landing_news_2_title_'+coinName),
                // text: localizeFilter('landing_news_2_text_'+coinName),
                // link: 'https://news.google.com/' }]
            } else {
                return []
            }
        }
    },
    methods: {
        async buyCoin(coinTicker) {
            if (this.$store.getters.isLoggined === true) {
                // await this.$store.dispatch(setBuyCoinTickerToProfile, {coinTicker})
                const {scope, email} = await this.$store.dispatch('getProfile')
                if (scope === 'not_activated_client') {
                    this.$router.push('/confirmEmail?email='+email)
                } else {
                    this.$router.push(`/cabinet?buyCoinTicker=${coinTicker}`)
                }
            } else {
                this.$router.push(`/login?buyCoinTicker=${coinTicker}`)
            }
        },
        handleScroll(event) {
            // const el = document.getElementById(this.chartSectionId)
            // if (el != null && el != undefined) {
            //     const position = el.getBoundingClientRect()
            //     if (position.y <= 300 && Math.abs(position.y) < position.height) {
            //         this.animateChart = true
            //         // setTimeout(()=>{
            //         //     this.animateChart = false
            //         // }, 10000)
            //     }
            // }
        }
    },
    setup() {
        
    },
    mounted() {
        window.addEventListener('scroll', this.handleScroll);
        this.showPopup = false
        setTimeout(()=>{
            this.$nextTick(()=>{
                this.showPopup = true
            })
        }, 2000)
    },
    // unmounted() {
    //     window.removeEventListener('scroll');
    // }
})
</script>

<style scoped>
.landing-coin {
    background: linear-gradient(90.06deg, #02111D 6.36%, #103A54 57.33%, #146672 83.15%);
    min-height: 100vh;
}

#contacts {
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), #1D2026;
    margin-top: 0!important;
}
</style>
