<template>
    <section class="coin-section">
    <div class="coin-section-container">
        <div class = "content-block">
            <transition name="fade" mode="out-in">
                <div key="one" class="dom-burn-top-block" v-if="showDomBurnViews">
                    <div class="title dom-burning-title" v-html="$localizeFilter('landing_buy_dom_coin_before_burning')"></div>
                    <div class="dom-burn-text time">{{$utils.formatTime(domBurnTimeLeft)}}
                        <img class="fire" src="@/assets/newImages/fire.png">
                    </div>
                </div>
                <div key="two" class="title" v-else>{{title}}</div>
            </transition>

            <!-- <div class="title" v-if="!showDomBurnViews">{{title}}</div> -->
                
            <img :src="require('@/assets/newImages/coins/'+coinName+'.png')" class="image">
            <div class="button-block" v-if="canBuy">
                 <a :href="buyCoinLink" target="_blank">
                    <button class="buy-button" @click="buyCoin(coin.ticker)">
                        <span style="text-shadow: 4px 7px 15px rgba(0, 0, 0, 0.29);">{{$localizeFilter('buy')}}</span>
                    </button>
                </a>
            </div>

            <transition name="collapse">
                <div class="dom-burn-bottom-block" v-if="showDomBurnViews">
                    <a :href="$store.state.info.domDetailsLink"  target="_blank">
                    <button class="basic-button">{{$localizeFilter('details_1')}}</button>
                    </a>
                    <a :href="$store.state.info.domBuyLink"  target="_blank">
                    <button class="basic-button buy-dom-button">
                        <label class="buy-dom-label">{{$localizeFilter('buy') + '!'}}</label>
                        <img class="buy-dom-image" src="@/assets/newImages/fire.png">
                        </button>
                    </a>
                </div>
            </transition>

        </div>
        <div class="text-block">
           <div class = "text text-header" v-if="textHeader != null && textHeader != undefined">{{textHeader}}</div>
           <div class = "text" :style="textStyle">{{text}}</div>
        </div>
        <div class="coin-statistics-table-block" v-if="showCoinStatistics">
            <CoinStatisticsTable class="coin-statistics-table" :coin="coin" @buyCoin="buyCoin" :buyCoinLink="buyCoinLink"/>
        </div>
    </div>
    </section>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import { ref } from "vue";
import CoinStatisticsTable from './CoinStatisticsTable.vue';

export default defineComponent({
    props: {
        coinName: String,
        title: String,
        textHeader: String,
        text: String,
        textStyle: String,
        buyCoinLink: String,
        showCoinStatistics: Boolean,
        canBuy: Boolean
    },
    components: {
        CoinStatisticsTable
    },
    data: () => ({
        showDomBurnViews: false
    }),
    setup() {
        
    },
    watch: {
        domBurnTimeLeft(newVal, oldVal) {
            this.$nextTick(() => {
                this.showDomBurnViews = (newVal > 0);
            });
        }
    },
    methods: {
        buyCoin(coinTicker) {
            this.$emit('buyCoin', coinTicker)
        }
        // insertAfter(newNode, existingNode) {
        //     existingNode.parentNode.insertBefore(newNode, existingNode.nextSibling);
        // },
    },
    computed: {
        coin() {
          return this.$store.getters.coinByName(this.coinName)
        },
        domBurnTimeLeft(){
             return this.$store.state.info.domBurnTimeLeft
        },
    },
    mounted() {
        if (this.coinName=='DOM' && this.domBurnTimeLeft > 0) {
            this.showDomBurnViews = true
        }
    }
});
</script>

